
.card {
    display: flex;
    overflow-y: hidden;
    overflow-x: scroll;
    padding: 30px;
}
.row {
    margin-left: 20px;
    color: white;
}

.row_posters {
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
    flex-wrap: nowrap;
    padding: 30px;
}

.row_posters::-webkit-scrollbar {
    display: none;
}

.row_poster {
    object-fit: contain; 
    max-height: 300px;
    margin-right: 10px;
    transition: transform 450ms;
}

.row_poster:hover {
    transform: scale(1.08);
}

.row_posterLarge {
    max-height: 400px
}

.row_posterLarge:hover {
    transform: scale(1.09);
}
